.quotation {
  padding: 80px 0 120px 0;
  &__container {
    display: flex;
    @include tablet {
      flex-direction: column;
      align-items: center;
    }
  }

  &__img {
    max-width: 525px;
    transform: translateY(110px);
    @include tablet {
      transform: translateY(0);
      margin-bottom: 45px;
    }
  }

  &__content {
    max-width: 735px;
    padding: 80px 105px 80px 135px;
    background-color: var(--color-white);
    box-shadow: 0px 80px 80px -20px rgba(154, 156, 165, 0.08),
      0px 30px 24px -10px rgba(154, 156, 165, 0.05),
      0px 12px 10px -6px rgba(154, 156, 165, 0.04),
      0px 4px 4px -4px rgba(30, 33, 44, 0.03);
    border-radius: 4px;
    @include small-tablet {
      padding: 30px;
    }
    @include mobile {
      padding-right: 5px;
    }
  }

  &__info {
    margin-bottom: 60px;
    position: relative;
    &::after {
      content: "";
      position: absolute;
      top: 5px;
      left: -58px;
      background-image: url(../img/braces.svg);
      background-position: center;
      background-repeat: no-repeat;
      width: 28px;
      height: 20px;
      @include small-tablet {
        width: 25px;
        height: 14px;
        left: -35px;
      }
    }
  }

  &__text {
    margin: 0;
    font-style: normal;
    font-weight: 700;
    font-size: 20px;
    line-height: 150%;
    color: var(--color-black);
    &:not(:last-child) {
      margin-bottom: 30px;
    }
    @include mobile {
      font-size: 15px;
    }
  }

  &__under {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__name {
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    line-height: 160%;
    color: var(--color-black);
    margin-bottom: 4px;
  }

  &__pos {
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 150%;
    color: var(--color-gray);
  }

  &__signature {
  }
}
