.work-inner-hero {
  padding: 155px 0 30px 0;
  &__title {
    margin-bottom: 60px;
    @include small-tablet {
      margin-bottom: 25px;
    }
  }
}
.work-inner-img {
  position: relative;
  &__arrow {
    width: 48px;
    height: 48px;
    padding: 12px 15px;
    background-color: rgb(#ffffff, 0.4);
    border-radius: 50%;
    z-index: 15;
    transition: 0.5s;
    &.swiper-button-disabled {
      background-color: rgb(#ffffff, 0.4);
      &:hover {
        background-color: rgb(#ffffff, 0.4);
      }
    }
    &:hover {
      background-color: rgb(#ffffff, 1);
    }
    svg {
      fill: var(--color-black);
      width: 18px;
      height: 11px;
    }
    @include mobile {
      display: none;
    }
  }
  &__prev {
    position: absolute;
    top: 40%;
    left: 50px;
    transform: translateY(-40%);
    @include small-tablet {
      left: 10px;
    }
  }
  &__next {
    position: absolute;
    top: 40%;
    right: 50px;
    transform: translateY(-40%);
    svg {
      transform: rotate(180deg);
    }
    @include small-tablet {
      right: 10px;
    }
  }
}
.work-slider {
  margin-bottom: 20px;
  &__item {
    width: 100%;
    height: 100%;
    max-height: 500px;
    img {
      width: 100%;
      height: 100%;
    }
  }
  @include mobile {
    height: 200px;
  }
}
.work-slider-nav {
  &-item {
    cursor: pointer;
    width: 100px;
    position: relative;
    img {
      width: 100%;
      height: 100%;
      border-radius: 4px;
    }
    &::after {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: var(--color-white);
      opacity: 0.6;
    }
    &.swiper-slide-thumb-active {
      &::after {
        display: none;
      }
    }
  }
}
