.project-goal {
  padding: 80px 0 150px 0;
  @include small-tablet {
    padding: 40px 0;
  }
  &__container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 35px;
    @include tablet {
      align-items: flex-start;
      flex-direction: column;
    }
  }

  &__left {
    max-width: 600px;
    @include tablet {
      max-width: none;
    }
  }

  &__title {
    margin-bottom: 40px;
  }

  &__text {
    margin: 0;
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 150%;
    color: var(--color-mako);
    &:not(:last-child) {
      margin-bottom: 25px;
    }
  }

  &__right {
    min-width: 300px;
    max-width: 495px;
    padding: 40px 60px;
    background-color: var(--color-white);
    box-shadow: 0px 80px 80px -20px rgba(154, 156, 165, 0.08),
      0px 30px 24px -10px rgba(154, 156, 165, 0.05),
      0px 12px 10px -6px rgba(154, 156, 165, 0.04),
      0px 4px 4px -4px rgba(30, 33, 44, 0.03);
    border-radius: 4px;
    @include tablet {
      align-self: center;
    }
    @include mobile {
      padding: 20px;
    }
  }
}
.project-info {
  &__item {
    display: flex;
    gap: 5px;
    &:not(:last-child) {
      margin-bottom: 15px;
    }
  }

  &__descr {
    width: 130px;
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    line-height: 160%;
    color: var(--color-black);
    @include mobile {
      width: 80px;
      font-size: 11px;
    }
  }

  &__value {
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 160%;
    color: var(--color-gray);
    @include mobile {
      font-size: 11px;
    }
  }
}
