.work-project {
  padding: 0 0 80px 0;
  &__container {
    transform: translateY(-50px);

    @include tablet {
      transform: translateY(0);
    }
  }
  @include tablet {
    padding-top: 40px;
  }
}
.work-project-tabs-nav {
  display: flex;
  gap: 20px;
  margin-bottom: 100px;
  &__item {
    max-width: 230px;
    width: 100%;
    @include mobile {
      flex-basis: 50%;
    }
  }

  &__btn {
    width: 100%;
    height: 100%;
    padding: 32px 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: var(--color-white);
    gap: 16px;
    box-shadow: 0px 80px 80px -20px rgba(154, 156, 165, 0.08),
      0px 30px 24px -10px rgba(154, 156, 165, 0.05),
      0px 12px 10px -6px rgba(154, 156, 165, 0.04),
      0px 4px 4px -4px rgba(30, 33, 44, 0.03);
    border-radius: 4px;

    svg {
      fill: var(--color-primary);
      width: 48px;
      height: 48px;
      pointer-events: none;
      @include small-tablet {
        width: 25px;
        height: 25px;
      }
    }
    &--active {
      background-color: var(--color-primary);
      pointer-events: none;
      svg {
        fill: var(--color-white);
      }
      .work-project-tabs-nav__text {
        color: var(--color-white);
      }
    }
    @include small-tablet {
      margin-bottom: 25px;
      padding: 15px;
    }
    @include mobile {
      padding: 5px;
    }
  }

  &__text {
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    line-height: 160%;
    color: var(--color-gray);
    pointer-events: none;
    @include small-tablet {
      font-size: 11px;
    }
  }
  @include mobile {
    gap: 0px;
    flex-wrap: wrap;
    margin-bottom: 25px;
  }
}

.work-project-tabs {
  display: flex;
  flex-wrap: wrap;
  gap: 30px;
  &__item {
    max-width: 390px;
    width: 100%;
    &--active {
      display: block !important;
    }
  }

  &__content {
    display: inline-block;
    transition: 0.5s;
    &:hover {
      box-shadow: 0px 80px 80px -20px rgba(154, 156, 165, 0.16),
        0px 30px 24px -10px rgba(154, 156, 165, 0.1),
        0px 12px 10px -6px rgba(154, 156, 165, 0.08),
        0px 4px 4px -4px rgba(30, 33, 44, 0.05);
      border-radius: 4px;
      .portfolio__slider-btn {
        opacity: 1;
      }
    }
  }
}
