.decisions {
  padding: 60px 0;
  &__container {
    display: flex;
    justify-content: space-between;
    gap: 35px;
    @include small-tablet {
      flex-direction: column;
    }
  }

  &__img {
    max-width: 495px;
    height: 550px;
    img {
      width: 100%;
      height: 100%;
    }
  }

  &__title {
    margin-bottom: 60px;
  }

  &__item {
    padding-left: 40px;
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 160%;
    color: var(--color-mako);
    background-image: url(../img/check.svg);
    background-repeat: no-repeat;
    background-position: center left;
    background-size: 20px;
    &:not(:last-child) {
      margin-bottom: 20px;
    }
  }
}
