.team {
  padding: 120px 0;
  @include tablet {
    padding: 60px 0;
  }
  &__title {
    text-align: center;
    margin-bottom: 25px;
  }

  &__sub-title {
    text-align: center;
    margin-bottom: 60px;
  }
}
.team-card {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 30px;
  @include tablet {
    grid-template-columns: repeat(3, 1fr);
  }
  @include small-tablet {
    grid-template-columns: repeat(2, 1fr);
  }

  @include mobile {
    grid-template-columns: repeat(1, 1fr);
  }
  &__item {
    &:hover {
      .card-overlay {
        max-height: 60px;
      }
    }
  }
  &__top {
    position: relative;
    overflow: hidden;
    img {
      vertical-align: bottom;
      width: 100%;
      height: 100%;
    }
  }

  &__info {
    padding: 16px 5px;
    text-align: center;
  }

  &__name {
    margin: 0;
    font-style: normal;
    font-weight: 700;
    font-size: 20px;
    line-height: 150%;
    color: var(--color-mako);
    margin-bottom: 5px;
  }

  &__pos {
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 160%;
    color: var(--color-gray);
  }
}
.card-overlay {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  background-color: rgb(#ff5a30, 0.7);
  border-radius: 0px 0px 4px 4px;
  max-height: 0;
  transition: 0.5s ease-in-out;

  &__list {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 20px 5px;
    gap: 13px;
  }

  &__link {
    display: block;
    svg {
      fill: var(--color-white);
      width: 20px;
      height: 20px;
      vertical-align: bottom;
    }
  }
}
