.history {
  padding: 80px 0;
  &__wrapper {
    display: flex;
    justify-content: space-between;
    @include tablet {
      flex-direction: column;
    }
  }

  &__left {
    margin-top: 60px;
  }
  &__nav {
    position: relative;
    &::after {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 1px;
      height: 100%;
      background-color: rgb(#9a9ca5, 0.5);
    }
  }
  &__item {
    &:not(:last-child) {
      margin-bottom: 20px;
    }
  }

  &__btn {
    position: relative;
    padding-left: 30px;
    font-style: normal;
    font-weight: 700;
    font-size: 28px;
    line-height: 150%;
    color: var(--color-gray);
    transition: 0.4s;
    &::after {
      content: "";
      position: absolute;
      top: 19px;
      left: -3px;
      width: 8px;
      height: 8px;
      border-radius: 100%;
      background-color: var(--color-gray);
      z-index: 5;
    }
    &--active {
      color: var(--color-primary);
      &::after {
        background-color: var(--color-primary);
      }
    }
  }

  &__right {
    max-width: 810px;
  }

  &__control {
    display: flex;
    align-items: center;
    gap: 15px;
    justify-content: flex-end;
    margin-bottom: 48px;
  }

  &__next {
    transform: rotate(180deg);
  }
  &__slider-img {
    min-height: 180px;
    margin-bottom: 25px;
  }
  &__slider-text {
    margin: 0;
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 160%;
    color: var(--color-mako);
  }
}
